<template><undefined>
<h1>Menu 导航菜单</h1>
<p>为页面和功能提供导航的菜单列表。</p>
<h2 id="he-shi-shi-yong"><a class="header-anchor" href="#he-shi-shi-yong" aria-hidden="true">¶</a> 何时使用</h2>
<p>导航菜单是一个网站的灵魂，用户依赖导航在各个页面中进行跳转。一般分为顶部导航和侧边导航，顶部导航提供全局性的类目和功能，侧边导航提供多级结构来收纳和排列网站架构。<br>
更多布局和导航的范例可以参考：<a href="https://ant.design/docs/spec/layout-cn">常用布局</a>。</p>
<h2 id="dai-ma-yan-shi"><a class="header-anchor" href="#dai-ma-yan-shi" aria-hidden="true">¶</a> 代码演示</h2>
<demo-box :jsfiddle="{&quot;html&quot;:&quot;&lt;template&gt;                   \n    &lt;v-menu mode=\&quot;horizontal\&quot; :data=\&quot;horizontalMnueData\&quot; @item-click=\&quot;itemclick\&quot;&gt;&lt;/v-menu&gt;\n&lt;/template&gt; \n\n&quot;,&quot;script&quot;:&quot;\nexport default {\n    data:()=&gt; ({\n        horizontalMnueData: [{\n            name: '导航一',\n            icon: 'mail'\n        },{\n            name: '导航二',\n            icon: 'appstore',\n            disabled: true\n        },{\n            name: '导航三-子菜单',\n            icon: 'setting',\n            groups: [{\n                groupName: \&quot;分组1\&quot;,\n                list: [{\n                    name: \&quot;选项1\&quot;\n                },{\n                    name: \&quot;选项2\&quot;\n                }]\n            }, {\n                groupName: \&quot;分组2\&quot;,\n                list: [{\n                    name: \&quot;选项3\&quot;\n                },{\n                    name: \&quot;选项4\&quot;\n                }]\n            }]\n        },{\n            name: '导航四-链接',\n            href: \&quot;http://www.alipay.com/\&quot;,\n            target: '_blank'\n        }]\n    })\n}\n&quot;,&quot;style&quot;:null}">
                  <div class="box-demo-show" slot="component"><template>                   
    <v-menu mode="horizontal" :data="horizontalMnueData" @item-click="itemclick"></v-menu>
</template> 

</div>
                  <div slot="description"><h4 id="ding-bu-dao-hang"><a class="header-anchor" href="#ding-bu-dao-hang" aria-hidden="true">¶</a> 顶部导航</h4>
<p>水平的顶部导航菜单。</p>
</div>
                  <div class="highlight" slot="code"><pre><code class="hljs language-html"><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>                   
    <span class="hljs-tag">&lt;<span class="hljs-name">v-menu</span> <span class="hljs-attr">mode</span>=<span class="hljs-string">"horizontal"</span> <span class="hljs-attr">:data</span>=<span class="hljs-string">"horizontalMnueData"</span>  <span class="hljs-attr">@item-click</span>=<span class="hljs-string">"itemclick"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">v-menu</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span> 
<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
<span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
    <span class="hljs-attr">data</span>:<span class="hljs-function"><span class="hljs-params">()</span>=&gt;</span> ({
        <span class="hljs-attr">horizontalMnueData</span>: [{
            <span class="hljs-attr">name</span>: <span class="hljs-string">'导航一'</span>,
            <span class="hljs-attr">icon</span>: <span class="hljs-string">'mail'</span>
        },{
            <span class="hljs-attr">name</span>: <span class="hljs-string">'导航二'</span>,
            <span class="hljs-attr">icon</span>: <span class="hljs-string">'appstore'</span>,
            <span class="hljs-attr">disabled</span>: <span class="hljs-literal">true</span>
        },{
            <span class="hljs-attr">name</span>: <span class="hljs-string">'导航三-子菜单'</span>,
            <span class="hljs-attr">icon</span>: <span class="hljs-string">'setting'</span>,
            <span class="hljs-attr">groups</span>: [{
                <span class="hljs-attr">groupName</span>: <span class="hljs-string">"分组1"</span>,
                <span class="hljs-attr">list</span>: [{
                    <span class="hljs-attr">name</span>: <span class="hljs-string">"选项1"</span>
                },{
                    <span class="hljs-attr">name</span>: <span class="hljs-string">"选项2"</span>
                }]
            }, {
                <span class="hljs-attr">groupName</span>: <span class="hljs-string">"分组2"</span>,
                <span class="hljs-attr">list</span>: [{
                    <span class="hljs-attr">name</span>: <span class="hljs-string">"选项3"</span>
                },{
                    <span class="hljs-attr">name</span>: <span class="hljs-string">"选项4"</span>
