<demo-box :jsfiddle="{&quot;html&quot;:&quot;&lt;template&gt;                \n    &lt;v-menu style=\&quot;width:240px\&quot; mode=\&quot;inline\&quot; :data=\&quot;inlineMenuData\&quot;&gt;&lt;/v-menu&gt;\n&lt;/template&gt;\n\n&quot;,&quot;script&quot;:&quot;\nexport default {\n    data:()=&gt; ({\n        inlineMenuData: [{\n            name: '导航一',\n            icon: 'mail',\n            groups: [{\n                groupName: \&quot;分组1\&quot;,\n                list: [{\n                    name: \&quot;选项1\&quot;\n                },{\n                    name: \&quot;选项2\&quot;\n                }]\n            }, {\n                groupName: \&quot;分组2\&quot;,\n                list: [{\n                    name: \&quot;选项3\&quot;\n                },{\n                    name: \&quot;选项4\&quot;\n                }]\n            }]\n        },{\n            name: '导航二',\n            icon: 'appstore',\n            children: [{\n                name: \&quot;选项5\&quot;\n            },{\n                name: \&quot;选项6\&quot;\n            },{\n                name: \&quot;三级导航\&quot;,\n                children: [{\n                    name: \&quot;选项7\&quot;\n                },{\n                    name: \&quot;选项8\&quot;\n                }]\n            }]\n        },{\n            name: '导航三',\n            icon: 'setting',\n            children: [{\n                name: \&quot;选项9\&quot;\n            },{\n                name: \&quot;选项10\&quot;\n            },{\n                name: \&quot;选项11\&quot;\n            },{\n                name: \&quot;选项12\&quot;\n            }]\n        }]\n    })\n}\n&quot;,&quot;style&quot;:null}">
