                <span class="hljs-attr">name</span>: <span class="hljs-string">"三级导航"</span>,
                <span class="hljs-attr">children</span>: [{
                    <span class="hljs-attr">name</span>: <span class="hljs-string">"选项7"</span>
                },{
                    <span class="hljs-attr">name</span>: <span class="hljs-string">"选项8"</span>
