<demo-box :jsfiddle="{&quot;html&quot;:&quot;&lt;template&gt;\n    &lt;div style=\&quot;width:240px\&quot;&gt;\n        &lt;v-button type=\&quot;primary\&quot; @click=\&quot;collapsed = !collapsed\&quot; style=\&quot;margin-bottom: 16px\&quot;&gt;\n            &lt;v-icon :type=\&quot;collapsed ? 'menu-unfold' : 'menu-fold'\&quot;&gt;&lt;/v-icon&gt;\n        &lt;/v-button&gt;\n        &lt;v-menu mode=\&quot;inline\&quot; :data=\&quot;collapsedMenuData\&quot; theme=\&quot;dark\&quot; :inline-collapsed=\&quot;collapsed\&quot;&gt;&lt;/v-menu&gt;\n    &lt;div&gt;\n&lt;/div&gt;&lt;/div&gt;&lt;/template&gt;\n\n&quot;,&quot;script&quot;:&quot;\nexport default {\n    data:()=&gt; ({\n        collapsed: false,\n        collapsedMenuData: [{\n            name: '选项1',\n            icon: 'mail',\n        }, {\n            name: '选项2',\n            icon: 'appstore',\n        }, {\n            name: '选项3',\n            icon: 'lock',\n        }, {\n            name: '导航1',\n            icon: 'setting',\n            children: [{\n                name: \&quot;选项4\&quot;\n            },{\n                name: \&quot;选项5\&quot;\n            },{\n                name: \&quot;选项6\&quot;\n            },{\n                name: \&quot;选项7\&quot;\n            }]\n        }, {\n            name: '导航2',\n            icon: 'desktop',\n            children: [{\n                name: \&quot;选项8\&quot;\n            },{\n                name: \&quot;选项9\&quot;\n            },{\n                name: \&quot;选项10\&quot;\n            }]\n        }]\n    })\n}\n&quot;,&quot;style&quot;:null}">
                  <div class="box-demo-show" slot="component"><template>
    <div style="width:240px">
        <v-button type="primary" @click="collapsed = !collapsed" style="margin-bottom: 16px">
            <v-icon :type="collapsed ? 'menu-unfold' : 'menu-fold'"></v-icon>
        </v-button>
        <v-menu mode="inline" :data="collapsedMenuData" theme="dark" :inline-collapsed="collapsed"></v-menu>
    <div>
</div></div></template>

</div>
                  <div slot="description"><h4 id="suo-qi-nei-qian-cai-dan"><a class="header-anchor" href="#suo-qi-nei-qian-cai-dan" aria-hidden="true">¶</a> 缩起内嵌菜单</h4>
