<demo-box :jsfiddle="{&quot;html&quot;:&quot;&lt;template&gt;\n    &lt;v-menu style=\&quot;width:240px\&quot; :data=\&quot;customMenuData\&quot;&gt;\n        &lt;template slot-scope=\&quot;{data}\&quot;&gt;\n            &lt;i v-if=\&quot;data.icon\&quot; :class=\&quot;'anticon anticon-' + data.icon\&quot;&gt;&lt;/i&gt;\n            &lt;router-link :to=\&quot;data.href\&quot; style=\&quot;display:inline\&quot;&gt;{{'item-' + data.name}}&lt;/router-link&gt;\n        &lt;/template&gt;\n        &lt;template slot-scope=\&quot;{data}\&quot; slot=\&quot;sub\&quot;&gt;\n            &lt;i v-if=\&quot;data.icon\&quot; :class=\&quot;'anticon anticon-' + data.icon\&quot;&gt;&lt;/i&gt;\n            &lt;span&gt;{{'sub-' + data.name}}&lt;/span&gt;\n        &lt;/template&gt;\n        &lt;template slot-scope=\&quot;{data}\&quot; slot=\&quot;group\&quot;&gt;\n            {{'group-' + data.groupName}}\n        &lt;/template&gt;\n    &lt;/v-menu&gt;\n&lt;/template&gt;\n\n&quot;,&quot;script&quot;:&quot;\nexport default {\n    data:()=&gt; ({\n        customMenuData: [{\n            name: \&quot;首页\&quot;,\n            icon: 'home',\n            selected: true,\n            disabled: true,\n            href: '/components/card'\n        },{\n            name: \&quot;安装指南\&quot;,\n            icon: 'mail',\n            children: [{\n                href: {\n                    name: 'menu',\n                    query: {\n                        src: 'http://test.api.g7s.chinawayltd.com/iframe.html#apilog/index.html'\n                    }\n                },\n                name: \&quot;快速上手\&quot;\n            },{\n                href: \&quot;/development\&quot;,\n                name: \&quot;开发指南\&quot;,\n                disabled: true\n            }]\n        },{\n            name: \&quot;基础组件\&quot;,\n            icon: 'folder',\n            groups: [{\n                groupName: \&quot;Basic\&quot;,\n                list: [{\n                    href: \&quot;/components/layout\&quot;,\n                    name: \&quot;布局\&quot;\n                }]\n            },{\n                groupName: \&quot;Form\&quot;,\n                list: [{\n                    href: \&quot;/components/radio\&quot;,\n                    name: \&quot;按钮\&quot;\n                }]\n            }]\n        }]\n    })\n}\n&quot;,&quot;style&quot;:null}">
                  <div class="box-demo-show" slot="component"><template>
    <v-menu style="width:240px" :data="customMenuData">
        <template slot-scope="{data}">
            <i v-if="data.icon" :class="'anticon anticon-' + data.icon"></i>
            <router-link :to="data.href" style="display:inline">{{'item-' + data.name}}</router-link>
        </template>
        <template slot-scope="{data}" slot="sub">
            <i v-if="data.icon" :class="'anticon anticon-' + data.icon"></i>
            <span>{{'sub-' + data.name}}</span>
        </template>
        <template slot-scope="{data}" slot="group">
            {{'group-' + data.groupName}}
        </template>
    </v-menu>
</template>

</div>
