<h2 id="api"><a class="header-anchor" href="#api" aria-hidden="true">¶</a> API</h2>
<h3 id="menu-props"><a class="header-anchor" href="#menu-props" aria-hidden="true">¶</a> Menu Props</h3>
<table class="table"><thead>
<tr>
<th>参数</th>
<th>说明</th>
<th>类型</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>data</td>
<td>可嵌套的节点属性的数组，生成menu的数据</td>
<td>Array</td>
<td>-</td>
</tr>
<tr>
<td>theme</td>
<td>主题颜色</td>
<td>String: <code v-pre="">light</code> <code v-pre="">dark</code></td>
<td>light</td>
</tr>
<tr>
<td>expand</td>
<td>设为true会展开所有第一级的菜单</td>
<td>Boolean</td>
<td>false</td>
</tr>
<tr>
<td>inlineCollapsed</td>
<td>inline 时菜单是否收起状态</td>
<td>Boolean</td>
<td>false</td>
</tr>
<tr>
<td>default:scopedSlot</td>
<td>自定义MenuItem Name的内容，传入的参数:data</td>
<td>Slot Node</td>
<td>-</td>
</tr>
<tr>
<td>sub:scopedSlot</td>
<td>自定义SubMenu Name的内容，传入的参数:data</td>
<td>Slot Node</td>
<td>-</td>
</tr>
<tr>
<td>group:scopedSlot</td>
<td>自定义Group Name的内容，传入的参数:data</td>
<td>Slot Node</td>
<td>-</td>
</tr>
</tbody>
</table>
<h3 id="menu-methods"><a class="header-anchor" href="#menu-methods" aria-hidden="true">¶</a> Menu Methods</h3>
<table class="table"><thead>
<tr>
<th>方法名</th>
<th>说明</th>
<th>参数</th>
<th>返回值</th>
</tr>
</thead>
<tbody>
<tr>
<td>setCheck</td>
<td>根据条件对象筛选节点，并设置节点的选中状态</td>
<td>conditions: Object,status: Boolean = true</td>
<td>isSuccess: Boolean</td>
</tr>
</tbody>
</table>
<h3 id="menu-events"><a class="header-anchor" href="#menu-events" aria-hidden="true">¶</a> Menu Events</h3>
<table class="table"><thead>
<tr>
<th>事件</th>
<th>说明</th>
<th>参数</th>
</tr>
</thead>
<tbody>
<tr>
<td>submenu-click</td>
<td>点击subMenu title时触发</td>
<td>被点击的submenu的数据</td>
</tr>
<tr>
<td>submenu-enter</td>
<td>鼠标移入subMenu title时触发</td>
<td>鼠标移入的submenu的数据</td>
</tr>
<tr>
<td>item-click</td>
<td>点击Menu Item时触发</td>
<td>从根到叶的数组，包含路径上的每个节点的数据</td>
</tr>
</tbody>
</table>
<h3 id="menu-data-prop"><a class="header-anchor" href="#menu-data-prop" aria-hidden="true">¶</a> Menu:Data Prop</h3>
<table class="table"><thead>
<tr>
<th>参数</th>
<th>说明</th>
<th>类型</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>name</td>
<td>标题</td>
<td>String</td>
<td>-</td>
</tr>
<tr>
<td>disabled</td>
<td>是否禁用</td>
<td>Boolean</td>
<td>false</td>
</tr>
<tr>
<td>expand</td>
<td>是否展开(只有SubMenu上可设置)</td>
<td>Boolean</td>
<td>false</td>
</tr>
<tr>
<td>selected</td>
<td>选中效果(只有MenuItem上可设置)</td>
<td>Boolean</td>
<td>false</td>
</tr>
<tr>
<td>icon</td>
<td>图标(只有MenuItem和SubMenu上可以设置)</td>
<td>String</td>
<td>-</td>
</tr>
<tr>
<td>href</td>
<td>链接(只有MenuItem上可设置)</td>
<td>String</td>
<td>-</td>
</tr>
<tr>
<td>target</td>
<td>打开方式（同<a>，(只有MenuItem上可设置)）</a></td>
<td>String</td>
<td>-</td>
</tr>
<tr>
<td>children</td>
<td>子菜单，同data</td>
<td>Object</td>
<td>-</td>
</tr>
<tr>
<td>groups</td>
<td>分组菜单</td>
<td>Object</td>
<td>-</td>
</tr>
</tbody>
</table>
<h3 id="menu-data-groups-prop"><a class="header-anchor" href="#menu-data-groups-prop" aria-hidden="true">¶</a> Menu:Data:groups Prop</h3>
<table class="table"><thead>
<tr>
<th>参数</th>
<th>说明</th>
<th>类型</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>groupName</td>
<td>标题</td>
<td>String</td>
<td>-</td>
</tr>
<tr>
<td>list</td>
<td>同data</td>
<td>Object</td>
<td>-</td>
</tr>
</tbody>
</table>
